    <!-- Document Upload -->
    <div class="bg-white border border-gray-200 rounded-lg shadow-sm">
      <div class="p-6 border-b border-gray-200">
        <div class="flex items-center gap-2">
          <Upload class="h-5 w-5 text-gray-700" />
