<script setup>
import { ref, computed, onMounted } from 'vue'
import { 
  Upload, 
  FileText, 
  Clock, 
  CheckCircle2, 
  User, 
  Building2,
  Save,
  Send,
  Download,
  AlertCircle,
  Loader2
} from 'lucide-vue-next'
import { useToast } from '@/composables/useToast'

// Toast notification
const { showToast } = useToast()

// Loading states
const isLoading = ref(false)
const isSubmitting = ref(false)

// RFP information
const rfpInfo = ref({
  rfpTitle: "Cloud Infrastructure Services RFP",
  rfpNumber: "RFP-2025-001",
  deadline: "August 1, 2025",
  budget: "$400,000 - $600,000"
})

// Form data
const formData = ref({
  companyName: "",
  contactName: "",
  email: "",
  phone: "",
  website: ""
})

// Invitation data
const invitationData = ref({
  rfpId: null,
  vendorId: null,
  org: "",
  vendorName: "",
  contactEmail: "",
  contactPhone: "",
  isOpenRfp: false
})

// RFP responses
const responses = ref({
  technical: "",
  implementation: "",
  pricing: "",
  timeline: "",
  experience: ""
})

// Completion status
const completionStatus = ref({
  company: 100,
  responses: 60,
  documents: 40,
  personnel: 80
})

// Evaluation criteria
const evaluationCriteria = [
  {
    id: "technical",
    title: "Technical Competence",
    weight: 40,
    description: "Demonstrate your technical capabilities for cloud infrastructure management",
    type: "narrative",
    required: true
  },
  {
    id: "implementation",
    title: "Implementation Approach",
    weight: 30,
    description: "Describe your methodology for implementing the solution",
    type: "narrative",
    required: true
  },
  {
    id: "pricing", 
    title: "Commercial Competitiveness",
    weight: 20,
    description: "Provide detailed pricing breakdown including costs, phases, and payment terms",
    type: "pricing",
    required: true
  },
  {
    id: "timeline",
    title: "Project Timeline",
    weight: 10,
    description: "Proposed project timeline and milestones",
    type: "timeline",
    required: true
  }
]

// Documents
const documents = ref([
  { name: "Company Profile", uploaded: true, required: true },
  { name: "Technical Certifications", uploaded: true, required: true },
  { name: "Reference Letters", uploaded: false, required: true },
  { name: "Insurance Certificate", uploaded: true, required: false },
  { name: "Financial Statements", uploaded: false, required: true }
])

// Key personnel
const keyPersonnel = ref([
  { role: "Project Manager", name: "Jane Doe", experience: "8 years", certifications: ["PMP", "ITIL"] },
  { role: "Technical Lead", name: "Mike Johnson", experience: "12 years", certifications: ["AWS Solutions Architect", "Kubernetes"] }
])

// Computed properties
const overallProgress = computed(() => {
  return Math.round(
    (completionStatus.value.company + completionStatus.value.responses + completionStatus.value.documents + completionStatus.value.personnel) / 4
  )
})

// API base URL
const API_BASE_URL = 'http://localhost:8000/api/v1'

// Methods
const parseQueryParameters = () => {
  const params = new URLSearchParams(window.location.search)
  
  // New parameter format
  const rfpId = params.get('rfpId') || ""
  const vendorId = params.get('vendorId') || ""
  const org = params.get('org') || ""
  const vendorName = params.get('vendorName') || ""
  const contactEmail = params.get('contactEmail') || ""
  const contactPhone = params.get('contactPhone') || ""
  
  // Check if it's an open RFP
  const pathParts = window.location.pathname.split('/')
  const isOpenRfp = pathParts.includes('open')
  
  invitationData.value = {
    rfpId,
    vendorId,
    org,
    vendorName,
    contactEmail,
    contactPhone,
    isOpenRfp
  }
  
  // Prefill form if parameters exist
  if (vendorName) formData.value.contactName = vendorName
  if (contactEmail) formData.value.email = contactEmail
  if (contactPhone) formData.value.phone = contactPhone
  if (org) formData.value.companyName = org
}

const fetchInvitationDetails = async () => {
  if (!invitationData.value.rfpId && !invitationData.value.isOpenRfp) {
    return
  }
  
  isLoading.value = true
  try {
    if (invitationData.value.isOpenRfp && invitationData.value.rfpId) {
      // Handle open RFP
      const response = await fetch(`${API_BASE_URL}/open-rfp/${invitationData.value.rfpId}/`)
      const data = await response.json()
      
      if (data.success) {
        rfpInfo.value = {
          rfpTitle: data.rfp.rfp_title,
          rfpNumber: data.rfp.rfp_number,
          deadline: data.rfp.submission_deadline ? new Date(data.rfp.submission_deadline).toLocaleDateString() : 'TBD',
          budget: data.rfp.estimated_value ? `$${parseInt(data.rfp.estimated_value).toLocaleString()}` : 'TBD'
        }
      }
    } else if (invitationData.value.rfpId) {
      // Handle RFP with new parameter format
      const params = new URLSearchParams({
        rfpId: invitationData.value.rfpId,
        vendorId: invitationData.value.vendorId,
        org: invitationData.value.org,
        vendorName: invitationData.value.vendorName,
        contactEmail: invitationData.value.contactEmail,
        contactPhone: invitationData.value.contactPhone
      })
      
      const response = await fetch(`${API_BASE_URL}/rfp-details/?${params}`)
      const data = await response.json()
      
      if (data.success) {
        const rfp = data.rfp
        const vendor = data.vendor
        
        rfpInfo.value = {
          rfpTitle: rfp.rfp_title,
          rfpNumber: rfp.rfp_number,
          deadline: rfp.submission_deadline ? new Date(rfp.submission_deadline).toLocaleDateString() : 'TBD',
          budget: rfp.estimated_value ? `$${parseInt(rfp.estimated_value).toLocaleString()}` : 'TBD'
        }
        
        // Update form data with vendor details
        if (vendor.vendor_name) formData.value.contactName = vendor.vendor_name
        if (vendor.contact_email) formData.value.email = vendor.contact_email
        if (vendor.contact_phone) formData.value.phone = vendor.contact_phone
        if (vendor.org) formData.value.companyName = vendor.org
      }
    }
  } catch (error) {
    console.error('Error fetching RFP details:', error)
    showToast('Error loading RFP details. Please refresh the page.', 'error')
  } finally {
    isLoading.value = false
  }
}

const fetchEvaluationCriteria = async () => {
  const rfpId = invitationData.value.rfpId
  if (!rfpId) return
  
  try {
    const response = await fetch(`${API_BASE_URL}/rfp/${rfpId}/evaluation-criteria/`)
    const data = await response.json()
    
    if (data.success) {
      // Update evaluation criteria with real data
      evaluationCriteria.value = data.criteria.map(criterion => ({
        id: criterion.criteria_id,
        title: criterion.criteria_name,
        weight: criterion.weight_percentage,
        description: criterion.criteria_description,
        type: criterion.evaluation_type === 'narrative' ? 'narrative' : 'text',
        required: criterion.is_mandatory
      }))
    }
  } catch (error) {
    console.error('Error fetching evaluation criteria:', error)
  }
}

const handleAutoSave = async () => {
  // Auto-save functionality can be implemented here
  console.log('Auto-saving progress...')
}

const handleSubmit = async () => {
  if (overallProgress.value < 90) {
    showToast('Please complete all required sections before submitting.', 'warning')
      return
    }
  
  isSubmitting.value = true
  
  try {
    // Prepare proposal data
    const proposalData = {
      companyInfo: formData.value,
      responses: responses.value,
      documents: documents.value,
      keyPersonnel: keyPersonnel.value,
      submissionDate: new Date().toISOString()
    }
    
    // Prepare submission data
    const submissionData = {
      rfpId: invitationData.value.rfpId,
      vendorId: invitationData.value.vendorId,
      org: formData.value.companyName,
      vendorName: formData.value.contactName,
      contactEmail: formData.value.email,
      contactPhone: formData.value.phone,
      proposalData: proposalData
    }
    
    const response = await fetch(`${API_BASE_URL}/rfp-responses/`, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify(submissionData)
    })
    
    const data = await response.json()
    
    if (data.success) {
      showToast('Proposal submitted successfully!', 'success')
      // Update completion status
      completionStatus.value.responses = 100
      completionStatus.value.documents = 100
      completionStatus.value.personnel = 100
    } else {
      showToast(data.error || 'Failed to submit proposal. Please try again.', 'error')
    }
  } catch (error) {
    console.error('Error submitting proposal:', error)
    showToast('Network error. Please check your connection and try again.', 'error')
  } finally {
    isSubmitting.value = false
  }
}

// Initialize component
onMounted(() => {
  parseQueryParameters()
  fetchInvitationDetails()
  fetchEvaluationCriteria()
})
</script>

